.wrapper {
  margin: 0 auto;
  padding: 10rem;
  padding-top: 3rem;
  box-sizing: border-box;
  max-width: 100%;
  max-height: 100vh;
  text-align: center;
  background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
  font-family: "Sen", sans-serif;
}
.title {
  font-size: 2rem;
}
input {
  padding: 0.3rem;
  border-radius: 5%;
  border: 1px solid gray;
  width: 150px;
  margin: 0.4rem;
}
button {
  background-color: teal;
  color: white;
  border-radius: 15%;
  padding: 0.25rem;
}
.img1 {
  width: 300px;
  height: 400px;
  margin: 2rem auto;
  text-align: center;
}
p {
  font-size: 0.7rem;
}
