.wrapper1 {
  max-width: 30%;
  max-height: 30%;
}

.flex {
  display: flex;
  flex-direction: column-reverse;
  text-align: center;
  margin: 0 auto;
}
.side {
  margin: 1rem;
}
.left-side {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}
.right-side h2 {
  font-size: 1.2rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid gray;
  align-items: center;
}
img {
  width: 250px;
  height: 225px;
  margin: 0 auto;
}
@media screen and (min-width: 250px) {
  img {
    width: 200px;
    height: 150px;
  }
}

@media screen and (min-width: 600px) {
  .flex {
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  img {
    width: 200px;
    height: 150px;
  }
}

@media screen and (min-width: 900px) {
  .wrapper {
    font-size: 1.5rem;
  }
  img {
    width: 300px;
    height: 250px;
    margin: 2rem;
  }
}
